const isArray = (x) => {
  return Object.prototype.toString.call(x) === '[object Array]';
};


export function not(val, arr = []) {
  if (!isArray(arr)) {
    arr = [arr];
  }
  let flag = true;

  arr.forEach((item) => {
    if (item === val) {
      return flag = false;
    }
  });

  return flag;
}

export function inside(val, arr = []) {
  if (!isArray(arr)) {
    arr = [arr];
  }
  let flag = false;

  arr.forEach((item) => {
    if (item === val) {
      return flag = true;
    }
  });

  return flag;
}

// 字符串长度限制
export function limit(str = '', opt = {}) {
  str = str.toString();
  if (opt.trim) {
    str = str.trim();
  }
  let min = parseInt(opt.min, 10) || -1;
  let max = parseInt(opt.max, 10) || Number.POSITIVE_INFINITY;

  return min <= str.length && max >= str.length;
}

const notAsciiWidth = 2;

// 字符长度限制（中文算1，2个数字和字母算1）
export function cLimit(str = '', opt = {}) {
  str = str.toString();
  if (opt.trim) {
    str = str.trim();
  }
  let length = 0;
  let min = parseInt(opt.min, 10) || -1;
  let max = parseInt(opt.max, 10) || Number.POSITIVE_INFINITY;

  for (let i = 0, l = str.length; i < l; i++) {
    length += str.charCodeAt(i) < 128 ? 1 : notAsciiWidth;
  }

  return length >= (notAsciiWidth * min) && length <= (notAsciiWidth * max);
}

// 数字大小限制
export function range(val, opt = {}) {
  val = parseInt(val, 10);
  if (isNaN(val)) {
    return false;
  }
  let min = parseInt(opt.min, 10);
  let max = parseInt(opt.max, 10);

  return !((min && min > val) || (max && max < val));
}

export function email(val) {
  return /^(\w)+(\.\w+)*@(\w)+((\.\w+)+)$/.test(val);
}

export function mobile(val) {
  return /^1\d{10}$/.test(val);
}

export function ascii(val) {
  return /^[\x00-\x7F]+$/.test(val);
}

export function asciiSingle(val) {
  return /[\x00-\xFF]/.test(val);
}

export function notAscii(val) {
  return /[^\x00-\x7F]/.test(val);
}

export function fullWidth(val) {
  return /[^\u0020-\u007E\uFF61-\uFF9F\uFFA0-\uFFDC\uFFE8-\uFFEE0-9a-zA-Z]/.test(val);
}

export function halfWidth(val) {
  return /[\u0020-\u007E\uFF61-\uFF9F\uFFA0-\uFFDC\uFFE8-\uFFEE0-9a-zA-Z]/.test(val);
}

export function uppercase(val) {
  return /[A-Z]/.test(val);
}

export function lowercase(val) {
  return /[a-z]/.test(val);
}

export function integer(val) {
  return /^\d+$/.test(val);
}

export function date(val) {
  return /^(?:20|19)\d\d([-/\\.])(?:0?[1-9]|1[0-2])\1(?:[012]?\d|3[01])$/.test(val);
}

export function time(val) {
  return /^(?:[01]?\d|2[0-3])[:：][0-5]?\d$/.test(val);
}

export function url(val) {
  return /^https?:\/\//.test(val);
}

export function ltrim(val) {
  return /^\s+/.test(val);
}

export function rtrim(val) {
  return /\s+$/.test(val);
}

export function uin(val) {
  return /^[1-9](\d{4,10}|\d{17})$/.test(val);
}

export function notEmpty(val) {
  return !(val == null || val === '');
}
