import React from 'react';
import AutoTable from './AutoTable';
import CollapseTable from './CollapseTable';

export default class AutoCollapseTable extends AutoTable {

  static defaultProps = AutoTable.defaultProps;

  renderTable = () => {
    const { visibleData, overlap } = this.state;
    return (
      <CollapseTable
        {...this.props.collapseTableProps}
        cols={this.renderCols()}
        data={visibleData}
        overlap={overlap}
      />
    );
  }
}
