// @require '../style/index.scss'
import React from 'react';
import classnames from 'classnames';

export default function TabPanel(props) {
  return (
    <div
      className={classnames('im-tab-panel', props.className)}
      key={props.tab}
      style={props.style}
    >
      {props.children}
    </div>
  );
}
