export function mockData(x, y, f = (a, b) => a + b) {
  let data = [];
  for (let i = 0; i < y; i++) {
    let row = [];
    for (let k = 0; k < x; k++) {
      row.push(f(i, k));
    }
    data.push(row);
  }
  return data;
}

/**
 * 模拟后端CGI
 * @param query
 * @returns {Promise}
 */
export function mockCGI(query = {
  sort: {},
  page: 0,
  count: 5,
}) {
  const { sort, page, count } = query;
  return new Promise((resolve) => {
    setTimeout(() => {
      let data = [];
      for (let i = 0; i < query.count; i++) {
        let x = page * count + i;
        let one = {
          A: String.fromCharCode(0x0150 + x),
          B: String.fromCharCode(0x0400 + x),
          C: String.fromCharCode(0x0620 + x),
          D: String.fromCharCode(0x0910 + x),
          sub: mockData(3, 2),
        };
        Object.keys(sort).forEach(key => {
          one[key] = String.fromCharCode(0x16A0 + x * sort[key]);
        });
        data.push(one);
      }
      resolve({
        total: 97,
        data,
      });
    }, 300);
  });
}
